<template>
  <div class="header">
    <p class="ta-c title">{{ titleName }}</p>
    <span class="time">{{ time }}</span>
  </div>
</template>

<script>
import { parseTime } from '@/utils/common';
export default {
  name: 'Header',
  props: {
    titleName: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      time: '',
      timer: null
    };
  },
  created() {
    this.timer = setInterval(() => {
      this.time = parseTime(new Date(), '{y}年{m}月{d}日 {h}:{i}:{s}');
    }, 1000);
  },
  destroyed() {
    this.timer && clearInterval(this.timer);
  }
};
</script>

<style lang="less" scoped>
p {
  margin: 0;
  padding: 0;
}
.header {
  position: relative;
  height: 8rem;
  background: url('~@/assets/img/kanban/title4.png') no-repeat;
  background-size: 100% 100%;
  text-align: center;

  .title {
    font-size: 3.6rem;
    line-height: 6rem;
    text-shadow: 3px 3px 3px #03a9f4;
    font-family: JLinXin;
    letter-spacing: 0.4rem;
  }
  .time {
    position: absolute;
    top: 0.8rem;
    right: 2rem;
    font-size: 2rem;
    color: #4bf3f9;
    font-family: Vox-Regular2;
  }
}
</style>
